<template>
  <card class="contract-card">
    <div class="contract-card_top">
      <div class="contract-card_top_title">
        <div class="contract-card_top_title-trade-id">
          Contract <span style="font-weight: 600">#{{ contractId }}</span>
        </div>
        <div class="contract-card_top_title-trade-product-name">
          "{{ contract.title }}"
        </div>
      </div>
      <ul class="contract-card_top_rules">
        <li>
          Do not confirm payment until you receive the product or service.
        </li>
        <li>
          Your chat is your contract, fully describe all the terms of your
          contract.
        </li>
        <li>Check the correctness of the transaction amount.</li>
        <li>Do not give your Trade PIN to anyone.</li>
        <li>
          If you have any questions feel free to call our support service.
        </li>
      </ul>
    </div>
    <div class="contract-card_bottom">
      <div class="contract-card_bottom_info">
        <div class="contract-card_bottom_info-title">
          <div class="contract-card_bottom_info-title-date">Date:</div>
          <div class="contract-card_bottom_info-title-price">Price:</div>
          <div class="contract-card_bottom_info-title-status">Status:</div>
        </div>
        <div class="contract-card_bottom_info-data">
          <div class="contract-card_bottom_info-data-date">
            {{ $time.format(contract.timestamp) }}
          </div>
          <div
            class="contract-card_bottom_info-data-price"
            style="font-weight: 600"
          >
            {{ contractAmount }}
          </div>
          <div class="contract-card_bottom_info-data-status">
            {{ contract.status }}
          </div>
        </div>
      </div>
      <button
        v-if="contract.buyer"
        @click="pay"
        :class="[
          'contract-card_bottom_submit',
          contract.status == 'completed' || paying ? 'completed' : '',
        ]"
      >
        Trade suceeded
      </button>
    </div>
  </card>
</template>

<script>
import Card from '../components/Card'
import { mapGetters } from 'vuex'

import axios from 'axios'

export default {
  name: 'contract-card',
  components: { Card },
  data: () => ({
    paying: false,
  }),
  computed: {
    ...mapGetters({
      isLoggedIn: 'auth/isLoggedIn',
      contract: 'guard/CONTRACT',
    }),
    contractId() {
      if (this.contract) {
        let t = this.contract.timestamp.toString()
        return t.substring(t.length - 6, t.length)
      } else {
        return ''
      }
    },
    contractAmount() {
      if (this.contract) {
        return this.contract.amount + ' ' + this.contract.symbol
      } else {
        return ''
      }
    },
  },
  methods: {
    pay() {
      if (this.paying) return

      this.paying = true
      axios
        .get(
          window.location.protocol +
            '//' +
            window.location.host +
            '/trade-guard/contract/pay?pin=' +
            this.contract.pin,
        )
        .then(response => {
          this.paying = false
          this.contract.status = 'completed'

          console.log(response.data)

          this.$store.commit('popups/ADD_ALERT', {
            type: 'info',
            title: 'Transfer succeed!',
            message:
              'You have just successfully paid ' +
              this.contract.amount +
              ' of ' +
              this.contract.symbol +
              '!',
          })
        })
        .catch(err => {
          if (err.response) {
            this.$store.commit('popups/ADD_ALERT', {
              type: 'error',
              title: 'Transfer failed',
              message: err.response.data.message,
            })
          } else {
            console.log(err)
          }

          this.paying = false
        })
    },
  },
  // mounted() {
  //   this.contract = this.$store.getters['guard/CONTRACT']
  // },
}
</script>

<style lang="scss" scoped>
@import '../../../../scss/smart-grid';
@import '../../../../scss/variables';

@keyframes attention {
  0% {
    box-shadow: 0 0 0px 0px $cyan;
  }

  20% {
    box-shadow: 0 0 10px 10px $cyan-alpha;
  }

  100% {
    box-shadow: 0 0 0px 0px $cyan;
  }
}

.contract-card {
  grid-area: contract;
  color: white;
  text-align: left;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &_top {
    &_title {
      font-family: Montserrat;
      margin-bottom: 2rem;

      &-trade-id {
        @include to(30rem) {
          font-size: 1rem;
        }

        @include from(30rem) {
          font-size: 1.6rem;
        }
      }

      &-trade-product-name {
        font-weight: 500;

        @include to(30rem) {
          font-size: 2rem;
        }

        @include from(30rem) {
          font-size: 2.6rem;
        }
      }
    }

    &_rules {
      color: $cyan;
      margin-left: 20px;
      width: 100%;

      @include to(30rem) {
        font-size: 0.8rem;
      }

      @include from(30rem) {
        font-size: 1rem;
      }

      li {
        @include to(30rem) {
          line-height: 1.3rem;
          margin: 5px;
        }

        @include from(30rem) {
          line-height: 1.6rem;
          margin: 13px;
        }
      }
    }
  }

  &_bottom {
    &_info {
      display: flex;
      width: 15rem;
      justify-content: space-between;

      @include to(30rem) {
        width: 100%;
        font-size: .8rem;
      }

      @include from(30rem) {
        width: 15rem;
        font-size: 1rem;
      }

      &-title {
        width: 5rem;
      }

      // &-data {
      // }

      &-title,
      &-data {
        div {
          margin: 15px 0;
        }
      }
    }

    &_submit {
      margin-top: 30px;
      width: 100%;
      cursor: pointer;
      font-size: 1.3rem;
      height: 3rem;
      transition: all 0.3s;
      outline: none;
      border: none;
      color: white;
      animation: none;
      background-color: $cyan;

      &:hover {
        background-color: transparent;
        animation-name: attention;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
      }
    }

    &_submit.completed {
      opacity: 0.5;
      cursor: default;

      &:hover {
        animation: none;
        background-color: $cyan;
      }
    }
  }
}
</style>
