<template>
  <div class="page">
    <Background />
    <div class="container">
      <contract-card />
      <div class="actions">
        <a
          title="Call Support"
          href="javascript:void(0)"
          @click="callSupport"
          v-if="contract.buyer"
          ><fa icon="headphones"
        /></a>
        <a title="Go Back" href="javascript:void(0)" @click="goBack">
          <fa icon="sign-out-alt" style="transform: rotate(180deg)" />
        </a>
      </div>
      <chat-card @send="sendMessage" :messages="contract.messages" />
    </div>
    <Footer />
  </div>
</template>

<script>
import Footer from '@/components/footer/Footer.vue'
import Background from '@/components/views/wallet/Background.vue'
import ContractCard from '@/components/views/trade-guard/cards/ContractCard'
import ChatCard from '@/components/views/trade-guard/cards/ChatCard'

import { mapGetters } from 'vuex'
import io from 'socket.io-client'

const url = window.location.href.split('/')
const socket = io(url[0] + '//' + url[2])

export default {
  name: 'trade-guard',
  components: { Footer, Background, ContractCard, ChatCard },
  computed: {
    ...mapGetters({
      profile: 'auth/profile',
      contract: 'guard/CONTRACT',
    }),
  },
  data: () => ({}),
  methods: {
    sendMessage(msg) {
      socket.emit('message', msg)
    },
    callSupport() {
      this.$store.commit('popups/SUPPORT')
    },
    goBack() {
      this.$router.go(-1)
    },
    disconnect() {
      this.goBack()
      this.$store.commit('popups/ADD_ALERT', {
        type: 'error',
        title: 'Contract Connection',
        message: "For some reason, you've been diconnected",
      })
    },
  },
  mounted() {
    socket.emit('join-contract', {
      email: this.profile.email,
      contract: this.contract,
    })

    socket.on('message', message => {
      this.contract.messages.push(message)
      this.$root.$emit('message')
    })

    socket.on('connected', () => {
      this.$store.dispatch('preloader/startAfterLoading')
    })

    socket.on('disconnected', () => {
      this.disconnect()
    })

    socket.on('disconnect', () => {
      this.disconnect()
    })

    this.$store.dispatch('preloader/startAfterLoading')
  },
}
</script>

<style lang="scss" scoped>
@import '../scss/smart-grid';
@import '../scss/variables';

.page {
  background: $v-grad;
  background-size: contain;
  padding-top: $header-slot;

  .container {
    z-index: 1;
    justify-content: center;
    padding: 0 20px;
    padding-bottom: 150px;
    display: grid;

    @include to(100rem) {
      max-width: 600px;
      grid-template-columns: 1fr;
      grid-template-rows: 4rem 1fr 1rem 1fr;
      grid-template-areas: '.' 'contract' '.' 'chat';
    }

    @include from(100rem) {
      grid-template-columns: 1.2fr 8rem 1fr;
      grid-template-rows: 1fr;
      grid-template-areas: 'contract . chat';
    }

    .actions {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 5px;
      overflow: hidden;

      @include to(100rem) {
        flex-direction: row;
      }
      @include from(100rem) {
        flex-direction: column;
      }

      a {
        justify-content: center;
        align-items: center;
        cursor: pointer;
        display: flex;
        width: 100%;
        height: 120px;
        font-size: 2rem;
        color: $cyan;
        background: #ffffff00;
        transition: all 0.2s;

        &:hover {
          color: white;
          background: #ffffff33;
        }
      }
    }
  }
}
</style>
